<script>
import {mapActions} from "vuex"
export default {
    name: 'card-games',
    props: {
        game:{
            type: Object
            },
        index:{
            type: Number,
        }
    },
    data: function(){
        return {
            nombre:"",
            opinion:""
            
            }
    },
    computed: {
        },
    methods: {
        ...mapActions(["addOpinion"]),
        getSubmitForm(){
            if(this.nombre === "" || this.opinion === ""){
                alert("Rellenar toda la información: nombre y opinión.")
                return
            }
            let myOpinion = {
                name: this.nombre,
                opinion: this.opinion,
                nameGame: this.game.name
            }
            this.addOpinion(myOpinion)
        }

    },
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
