import Vue from 'vue'
import Vuex from 'vuex' 
import games from "./games.json" 


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    games, 
    opiniones:[]
  },
  getters: {
    countOpiniones(state){
      return state.opiniones.length
    },
    
  },
  mutations: {
    ADD_OPINION(state,newOpinion){
      state.opiniones.push(newOpinion)
    },
    SAVE_OPINION(state,params){
      state.opiniones.splice(params.index, 1, params.opinion)
    },
    DELETE_OPINION(state,index){ 
      state.opiniones.splice(index,1)
    }
  },  
  actions: {
    addOpinion({commit}, newOpinion){
      commit("ADD_OPINION",newOpinion)
    },
    saveOpinion({commit}, params){
      commit("SAVE_OPINION",params)
    },
    deleteOpinion({commit},index){
      commit("DELETE_OPINION",index)
    }
    
  },
  modules: {
  }
})
